<template>
  <div>
    <h1>I类道路检测</h1>
    <el-row>

    </el-row>
    <el-row>
      <div class="grid-content bg-purple-light">
        <el-table
          :data="tableData.filter(data => !search || data.name.toLowerCase().includes(search.toLowerCase()))"
          stripe="true"
          style="width: 100%"
          max-height="540">
          <el-table-column
            align="center"
            label="道路名称"
            width="250">
            <template slot-scope="scope">
              <el-popover trigger="hover" placement="top">
                <p>姓名: {{ scope.row.name }}</p>
                <p>住址: {{ scope.row.address }}</p>
                <div slot="reference" class="name-wrapper">
                  <el-tag size="medium">{{ scope.row.name }}</el-tag>
                </div>
              </el-popover>
            </template>
          </el-table-column>
          <el-table-column
            align="center"
            label="道路状态"
            width="250">
            <p>正常</p>
          </el-table-column>
          <el-table-column
            align="center"
            label="最近巡查日期"
            width="180">
            <template slot-scope="scope">
              <i class="el-icon-time"></i>
              <span style="margin-left: 10px">{{ scope.row.date }}</span>
            </template>
          </el-table-column>
          <el-table-column
            align="center"
            prop="check"
            label="是否需要检测"
            width="250"
            :filters="[{ text: '是', value: '是' }, { text: '否', value: '否' }]"
            :filter-method="filterTag"
            filter-placement="bottom-end">
            <p>否</p>
          </el-table-column>
          <el-table-column
            align="center">
            <template slot="header" slot-scope="scope">
              <el-input
                v-model="search"
                size="mini"
                placeholder="输入关键字搜索"/>
            </template>
            <template slot-scope="scope">
              <el-button
                size="mini"
                @click="handleEdit(scope.$index, scope.row)">巡查记录</el-button>
              <el-button
                size="mini"
                type="warning"
                @click="handleAdd(scope.$index, scope.row)">添加预警</el-button>
              <el-button
                size="mini"
                type="danger"
                @click="handleDelete(scope.$index, scope.row)">取消预警</el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </el-row>

    <el-dialog title="设施损害通知单" :visible.sync="dialogTableVisible">
      <el-row>
        <el-col :span="8"><div class="grid-content bg-purple">
          <el-input placeholder="请输入内容" v-model="input1">
            <template slot="prepend">Http://</template>
          </el-input>
        </div></el-col>
        <el-col :span="8"><div class="grid-content bg-purple-light">
          <el-input placeholder="请输入内容" v-model="input1">
            <template slot="prepend">Http://</template>
          </el-input>
        </div></el-col>
        <el-col :span="8"><div class="grid-content bg-purple">
          <el-input placeholder="请输入内容" v-model="input1">
            <template slot="prepend">Http://</template>
          </el-input>
        </div></el-col>
      </el-row>
      <el-row :gutter="20">
        <el-col :span="16"><div class="grid-content bg-purple">
          <el-input placeholder="请输入内容" v-model="input1">
            <template slot="prepend">Http://</template>
          </el-input>
        </div></el-col>
        <el-col :span="8"><div class="grid-content bg-purple">
          <el-input
            placeholder="请输入内容"
            suffix-icon="el-icon-date"
            v-model="input1">
          </el-input>
        </div></el-col>
      </el-row>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        tableData: [],
        search: '',
        table_index: undefined,
        table_row: undefined,
        dialogTableVisible: false
      }
    },
    mounted:function () {
      this.loadTableData();
    },
    methods: {
      handleEdit(index, row) {
        console.log(index, row);
      },
      handleDelete(index, row) {
        console.log(index, row);
      },
      handleAdd(index, row){
        console.log(index, row);
        var _this = this;
        _this.table_index = index;
        _this.table_row = row;
        _this.dialogTableVisible = true;
      },
      loadTableData(){
        var _this = this;
        this.getRequest("/rc/c1r").then(resp =>{
          if (resp && resp.status == 200){
            var data = resp.data;
            _this.tableData = data.tableData;
          }
        })
      },
      filterTag(value, row) {
        return row.check === value;
      }
    }
  }
</script>

<style>

</style>




